

@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@500;600;700&display=swap");



:root {
    /* fonts */
    --font-poppins: Poppins;

    /* font sizes */
    --font-size-3xs: 10px;
    --font-size-mini: 15px;

    /* Colors */
    --color-white: #fff;
    --color-darkslategray: #393939;

    /* Gaps */
    --gap-41xl: 60px;
    --gap-0: 0px;
}


