}

.cardDetailsDiv {
    flex: 1;
    align-items: center;
    gap: 60px 0;
    max-width: 100%;
    z-index: 2;
}

.nterviewCardpass,
.taizoinInterviewCardpassB {
    background-color: var(--color-white);
    overflow: hidden;
    flex-shrink: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    box-sizing: border-box;
    max-width: 100%;
}

.nterviewCardpass {
    width: 395px;
    margin: 0 !important;
    position: absolute;
    top: calc(50% - 254.5px);
    left: calc(50% - 197px);
    border-radius: 8px;
    box-shadow: 0 5px 10px 4px rgba(0, 0, 0, 0.12);
    align-items: center;
    padding: 24px 22px 25px 23px;
    z-index: 1;
}

.taizoinInterviewCardpassB {
    width: 716px;
    height: 711px;
    align-items: flex-end;
    padding: 101px 10px 64px 16px;
    position: relative;
    gap: 0 10px;
}

.sendWhatsapp {
    width: 132px;
    position: relative;
    font-size: 14px;
    font-weight: 600;
    font-family: Poppins;
    color: var(--color-white);
    // text-align: center;
    display: inline-block;
}

.sendWhatsappButton,
.taizoinCandidateInterviewC {
    overflow: hidden;
    display: flex;
    align-items: center;
    box-sizing: border-box;
}

.sendWhatsappButton {
    cursor: pointer;
    border: 0;
    padding: 10px;
    background-color: #169c50;
    width: 156px;
    border-radius: 40px;
    flex-direction: row;
    justify-content: center;
    white-space: nowrap;
}

.sendWhatsappButton:hover {
    background-color: #30b56b;
}

.taizoinCandidateInterviewC {
    width: 100%;
    height: 985px;
    position: relative;
    background-color: var(--color-white);
    flex-direction: column;
    justify-content: flex-start;
    // padding: 80px 20px 93px;
    gap: 60px 0;
    letter-spacing: normal;
    // text-align: center;
    font-size: 30px;
    color: var(--color-darkslategray);
    font-family: Poppins;
}

.fontPoppins{
    font-family: Poppins !important;
}

@media screen and (max-width: 750px) {
    .interviewCard {
        font-size: 24px;
    }

    .taizoinInterviewCardpassB {
        height: auto;
        min-height: 711;
    }

    .taizoinCandidateInterviewC {
        height: auto;
        gap: 60px 0;
    }
}

@media screen and (max-width: 450px) {
    .interviewCard {
        font-size: 18px;
    }

    .div,
    .div1,
    .div2,
    .div3,
    .div5,
    .spacer {
        width: 100%;
        height: 4px;
    }
}